___CSS_LOADER_EXPORT___.push([module.id, "\n*[data-v-4eaf4342] {\n    font-family: \"Roboto\";\n}\n.container[data-v-4eaf4342] {\n      padding: 20px;\n}\nul[data-v-4eaf4342] {\n    list-style-type: none;\n}\n.createLi[data-v-4eaf4342] {\n    cursor: pointer;\n}\n.move-to-item[data-v-4eaf4342] {\n    padding-top: 1px;\n    padding-right: 20px;\n}\n.move-to-item[data-v-4eaf4342]:not(.item-disabled):hover {\n    background: #D5D5D5;\n}\n.item-disabled[data-v-4eaf4342] {\n    opacity: 0.5;\n}\n.buttons-div[data-v-4eaf4342] {\n      display: flex;\n      justify-content: end;\n      margin-top: 52px;\n      padding: 0 20px;\n}\n.cancel-button[data-v-4eaf4342] {\n      font-weight: 500;\n      font-size: 16px;\n      text-align: center;\n      color: #757575;\n      display: flex;\n      align-items: center;\n      padding: 8px 16px;\n      border-radius: 4px;\n      margin-right: 4px;\n      border: none;\n      background: transparent;\n}\n.move-button[data-v-4eaf4342] {\n      font-weight: 500;\n      font-size: 16px;\n      text-align: center;\n      color: #FFFFFF;\n      background: #151233;\n      display: flex;\n      align-items: center;\n      padding: 8px 16px;\n      border-radius: 4px;\n      border: none;\n}\n.move-button[data-v-4eaf4342]:disabled {\n      color: #757575;\n}\n\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/dialogs/moveItem.vue"],"names":[],"mappings":";AA4GE;IACE,qBAAqB;AACvB;AACA;MACI,aAAa;AAEjB;AACA;IACE,qBAAqB;AACvB;AACA;IACE,eAAe;AACjB;AAEA;IACE,gBAAgB;IAChB,mBAAmB;AACrB;AAEA;IACE,mBAAmB;AACrB;AAEA;IACE,YAAY;AACd;AACA;MACI,aAAa;MACb,oBAAoB;MACpB,gBAAgB;MAChB,eAAe;AACnB;AACA;MACI,gBAAgB;MAChB,eAAe;MACf,kBAAkB;MAClB,cAAc;MACd,aAAa;MACb,mBAAmB;MACnB,iBAAiB;MACjB,kBAAkB;MAClB,iBAAiB;MACjB,YAAY;MACZ,uBAAuB;AAC3B;AACA;MACI,gBAAgB;MAChB,eAAe;MACf,kBAAkB;MAClB,cAAc;MACd,mBAAmB;MACnB,aAAa;MACb,mBAAmB;MACnB,iBAAiB;MACjB,kBAAkB;MAClB,YAAY;AAChB;AACA;MACI,cAAc;AAClB","sourcesContent":["<script setup lang=\"ts\">\nimport { onUnmounted, ref, onMounted } from \"vue\";\nimport { PathHelper } from \"../../../../../../common/helpers/pathHelper\";\nimport { container } from '../../../../domain/core/diContainer';\nimport { IMoveItemInline } from '../../../../domain/presentation/dialogs/IMoveItemProps';\nimport { ITreeItem, ITreeView } from \"../../../../domain/presentation/ITreeView\";\nimport { FileDisplayerExternalData, IFileDisplayerUseCase } from \"../../../../domain/useCase/IFileDisplayerUseCase\";\nimport { FileExplorerConstants, IFileExplorer } from \"../../../../domain/useCase/IFileExplorer\";\nimport { IMoveTargetRequired, IMoveUseCase } from '../../../../domain/useCase/IMoveUseCase';\nimport { IUseCaseExecutor } from '../../../../domain/useCase/IUseCaseExecutor';\nimport { IViewModel } from '../../../../domain/viewModel/IViewModel';\nimport { IViewModelManager } from \"../../../../domain/viewModel/IViewModelManager\";\nimport { IQJsonAdditionals } from \"../../../../../../common/qCloudTemp/quickCloud\";\nimport StudioInfo from \"../ui/studioInfoComp.vue\"\n\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst moveUseCase = container.resolve<IMoveUseCase>(IMoveUseCase);\nconst param = defineProps<{ param: IMoveItemInline }>();\nconst props = param.param;\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst viewModelManager = container.resolve<IViewModelManager>(IViewModelManager);\nconst emit = defineEmits([\"close\"]);\nconst rootDiv = ref<HTMLDivElement>();\nconst fileDisplayerUC = container.resolve<IFileDisplayerUseCase>(IFileDisplayerUseCase);\nconst fileExplorer = container.resolve<IFileExplorer>(IFileExplorer);\n\nlet selected: ITreeItem<FileDisplayerExternalData> | undefined = undefined;\nlet treeView: ITreeView<FileDisplayerExternalData>;\nconst selectedIsRootPage = ref<boolean>(false);\nonMounted(() => {\n  const app = viewModelManager.getCurrentApplication()!;\n  if (props.itemToBeMoved.objectType == \"folder\") {\n    fileExplorer.cloneFilterFolderItems(props.itemToBeMoved.ID, (item) => {\n      if (selectedIsRootPage.value) { return false; }\n      if (item.externalData.objectType != \"model\") { return true; }\n      if ((item.externalData.additionals as IQJsonAdditionals | undefined)?.isRootPage) { selectedIsRootPage.value = true; }\n      return false;\n    });\n  } else {\n    selectedIsRootPage.value = (props.itemToBeMoved.additionals as IQJsonAdditionals | undefined)?.isRootPage || false;\n  }\n  treeView = fileDisplayerUC.createFileExplorerClone(rootDiv.value!,\n  {\n    displayFilterer: item => {\n      if (selectedIsRootPage.value && item.objectType == \"module\") { return false; }\n      if (item.objectType == \"module\" && item.shareType == \"version\" && item.mainOwner != app.ID) { return false; } // can not move to version imported modules\n      if (item.objectType == \"model\"){ return false; }\n      if (item.ID == FileExplorerConstants.uiSettingsID) { return false; }\n      return true;\n    },\n    cb: { select: item => selected = item },\n    additionalRoot: { externalData: app, id: app.ID, name: app.name },\n  });\n});\n\nonUnmounted(() => {\n  treeView.reset();\n});\n\nasync function move() {\n  await executor.execute(async () => {\n    if (!selected) { return; }\n    let target: Partial<IMoveTargetRequired>;\n    switch (selected.externalData.objectType) {\n      case \"model\": throw new Error(\"unexpected target\");\n      case \"folder\":\n        const folder = selected.externalData;\n        target = {\n          address: { name: props.itemToBeMoved.name, path: PathHelper.join(folder.path , folder.name) },\n          owner: folder.owner.ID != props.itemToBeMoved.owner.ID ? { id: folder.owner.ID, type: folder.owner.objectType } : undefined,\n        };\n        break;\n      case \"module\":\n        const module = selected.externalData;\n        target = { owner: { id: module.ID, type: \"module\" }, address: { path: \"/\", name: \"\" } };\n        break;\n      case \"application\":\n        const application = selected.externalData;\n        target = { owner: { id: application.ID, type: \"application\" }, address: { path: \"/\", name: \"\" } };\n        break;\n    }\n    moveUseCase.checkMergeable(props.itemToBeMoved, target)\n    \n    emit(\"close\");\n  }, { loading: true });\n}\n\nfunction close() {\n  emit(\"close\");\n}\n\n</script> \n\n<template>\n  <div class=\"container\">\n    <StudioInfo v-if=\"selectedIsRootPage\" style=\"margin:20px 0px\" :infoText=\"'Selected items contains the start screen page. Modules are hidden because they cannot contain the start screen page.'\"></StudioInfo>\n    <div id=\"content-area\" class=\"popover__content\">\n      <div ref=\"rootDiv\" style=\"height: 100%\" class=\"errorDiv\"></div>\n      <div class=\"buttons-div\">\n        <button class=\"cancel-button\" @click=\"close()\">Cancel</button>\n        <button class=\"move-button\" @click=\"move()\">MOVE</button>\n      </div>\n    </div>\n  </div>\n</template>\n\n\n<style scoped>\n  * {\n    font-family: \"Roboto\";\n  }\n  .container {\n      padding: 20px;\n      \n  }\n  ul {\n    list-style-type: none;\n  }\n  .createLi {\n    cursor: pointer;\n  }\n\n  .move-to-item {\n    padding-top: 1px;\n    padding-right: 20px;\n  }\n\n  .move-to-item:not(.item-disabled):hover {\n    background: #D5D5D5;\n  }\n\n  .item-disabled {\n    opacity: 0.5;\n  }\n  .buttons-div {\n      display: flex;\n      justify-content: end;\n      margin-top: 52px;\n      padding: 0 20px;\n  }\n  .cancel-button {\n      font-weight: 500;\n      font-size: 16px;\n      text-align: center;\n      color: #757575;\n      display: flex;\n      align-items: center;\n      padding: 8px 16px;\n      border-radius: 4px;\n      margin-right: 4px;\n      border: none;\n      background: transparent;\n  }\n  .move-button {\n      font-weight: 500;\n      font-size: 16px;\n      text-align: center;\n      color: #FFFFFF;\n      background: #151233;\n      display: flex;\n      align-items: center;\n      padding: 8px 16px;\n      border-radius: 4px;\n      border: none;\n  }\n  .move-button:disabled {\n      color: #757575;\n  }\n\n</style>\n\n"],"sourceRoot":""}]);
